<template>
  <div v-if="!isInternalNode">
    <h2>Zkladn informcie o uzle</h2>
    Samotn truktra ST-Trees nepracuje s tmito atribtmi. Sli to ako
    npoveda pre pouivatea, v prpade, e by mu graf priiel neprehadn.
    <ol>
      <li><b>Rodi:</b> Uzol, ktor je rodi pre dan uzol</li>
      <li><b>Size:</b> Oznauje poet vetk potomkov uzla + 1</li>
      <li><b>Potomkovia:</b> Vetky uzly, ktor s potomkom danho uzla</li>
    </ol>
    <div class=""></div>
    <h2>Npoveda o truktre vonkajieho uzla</h2>
    <p>
      Uzol, ktor je zakreslen v grafe. Vonkajie uzly tvoria listy vo
      virtulnom strome. Prvm listom je najspodnej uzol v ceste, pretoe
      cesty zapisujeme zdola nahor. Poet listov sa rovn potu uzlov v ceste.
      Kad list m uloen odkaz na svojho rodia (vntorn uzol) pomocou
      hodnoty bparent. Rodiom pre vonkaj uzol je vntorn uzol. Taktie kad
      list m uloen, do ktorej cesty patr ako bpath.
    </p>
    <p>
      <span
        >truktra vonkajieho uzla zvisi od zvolenho spsobu dtovej
        truktry.
        <br />
        Aktulne mte zvolen spsob:
        <b>{{ sizeStruct ? "Partitioning by Size" : "Naive Partitioning" }}</b>
      </span>
      <br />
      <span>Ukka vetkch atribtov, ktor dan uzol me nadobda:</span>
      <img v-if="sizeStruct" class="image" src="./outerNodeSize.png" alt="" />
      <img v-if="!sizeStruct" class="image" src="./outerNodeNaive.png" alt="" />
    </p>
  </div>
  <div v-if="isInternalNode">
    <h2>Npoveda o vntornom uzle</h2>
    <p>
      Uzol nie je zakreslen v grafe. Vidme ho iba pri vykreslen virtulneho
      stromu. Kad vntorn uzol symbolizuje jednu hranu z cesty. truktra
      vntornho uzla zle poda typu pouitej dtovej truktry: Naive
      partitioning alebo Partition by Size. Tieto truktry si detailnejie
      predstavme neskr v prci. Prve vaka tmto uzlom sme schopn rchlo a
      efektvne vykonva opercie na stromoch. Vntorn uzol x reprezentuje
      hranu medzi vonkajmi vrcholmi y a z, pre ktor plat e y = successor(x)
      a z = ancestor(x).
    </p>
    <p>
      <span
        >truktra vntornho uzlu zvisi od zvolenho spsobu dtovej
        truktry.
        <br />
        Aktulne mte zvolen spsob:
        <b>{{ sizeStruct ? "Partitioning by Size" : "Naive Partitioning" }}</b>
      </span>
      <br />
      <span>Ukka vetkch atribtov, ktor dan uzol me nadobda</span>
      <img v-if="sizeStruct" class="image" src="./insideNodeSize.png" alt="" />
      <img
        v-if="!sizeStruct"
        class="image"
        src="./insideNodeNaive.png"
        alt=""
      />
    </p>
  </div>
</template>

<script>
export default {
  props: {
    sizeStruct: Boolean,
    isInternalNode: Boolean,
  },
};
</script>

<style>
.image {
  width: 100%;
}
</style>